import React, { useState, useEffect } from 'react';
import { Box, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button, IconButton } from '@mui/material';
import { Link } from 'react-router-dom';
import { getCompareList, removeFromCompare, clearCompare } from '../utils/compare';

const characteristics = [
  { label: '', key: 'image', isImage: true },
  { label: '', key: 'name' },
  { label: '', key: 'price', format: (v) => v + ' ' },
  { label: '', key: 'resolution' },
  { label: '-', key: 'ir' },
  { label: '', key: 'power' },
];

const Compare = () => {
  const [products, setProducts] = useState([]);

  useEffect(() => {
    let mounted = true;
    async function load() {
      const list = await getCompareList();
      if (mounted) setProducts(list);
    }
    load();
    const onChange = () => load();
    window.addEventListener('compareChanged', onChange);
    window.addEventListener('storage', onChange);
    return () => {
      mounted = false;
      window.removeEventListener('compareChanged', onChange);
      window.removeEventListener('storage', onChange);
    };
  }, []);

  const handleRemove = async (id) => {
    await removeFromCompare(id);
    window.dispatchEvent(new Event('compareChanged'));
  };

  const handleClear = async () => {
    await clearCompare();
    window.dispatchEvent(new Event('compareChanged'));
  };

  if (!products.length) {
    return (
      <Box sx={{ background: '#f5f7fa', minHeight: '60vh', py: 4, textAlign: 'center' }}>
        <Typography variant="h4" sx={{ fontWeight: 700, mb: 3 }}>
           
        </Typography>
        <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
            .      .
        </Typography>
        <Button variant="contained" color="primary" component={Link} to="/catalog">
           
        </Button>
      </Box>
    );
  }

  return (
    <Box sx={{ background: '#f5f7fa', minHeight: '60vh', py: 4 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, textAlign: 'center' }}>
         
      </Typography>
      <Box sx={{ textAlign: 'right', mb: 2, maxWidth: 900, mx: 'auto' }}>
        <Button variant="outlined" color="error" size="small" onClick={handleClear}>
           
        </Button>
      </Box>
      <TableContainer component={Paper} sx={{ maxWidth: 900, mx: 'auto' }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell sx={{ fontWeight: 700, width: 200 }}></TableCell>
              {products.map((product) => (
                <TableCell key={product.id} align="center" sx={{ fontWeight: 700 }}>
                  <IconButton color="error" onClick={() => handleRemove(product.id)} title="  "></IconButton>
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {characteristics.map((char) => (
              <TableRow key={char.key}>
                <TableCell sx={{ fontWeight: 500 }}>{char.label}</TableCell>
                {products.map((product) => (
                  <TableCell key={product.id} align="center">
                    {char.isImage ? (
                      <img src={product[char.key]} alt={product.name} style={{ maxHeight: 60 }} />
                    ) : char.format ? (
                      char.format(product[char.key])
                    ) : (
                      product[char.key] || <span style={{ color: '#bbb' }}></span>
                    )}
                  </TableCell>
                ))}
              </TableRow>
            ))}
            <TableRow>
              <TableCell></TableCell>
              {products.map((product) => (
                <TableCell key={product.id} align="center">
                  <Button variant="outlined" size="small" component={Link} to={`/product/${product.id}`}></Button>
                </TableCell>
              ))}
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default Compare; 