import React from 'react';
import { Typography, List, ListItem, ListItemText, Container, Paper, Link as MuiLink } from '@mui/material';
import { Link } from 'react-router-dom';

const siteLinks = [
  { label: '', to: '/' },
  { label: '', to: '/catalog' },
  { label: '', to: '/cart' },
  { label: '', to: '/brands' },
  { label: '', to: '/prices' },
  { label: '', to: '/company' },
  { label: '', to: '/contacts' },
  { label: '', to: '/services' },
  { label: '', to: '/sale' },
  { label: '', to: '/login' },
  { label: '', to: '/register' },
];

const SiteMap = () => (
  <Container maxWidth="md" sx={{ my: 8 }}>
    <Paper elevation={3} sx={{ p: 4, borderRadius: 3 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, textAlign: 'center', color: '#1976d2' }}>
         
      </Typography>
      <List>
        {siteLinks.map((item) => (
          <ListItem key={item.to}>
            <ListItemText>
              <MuiLink component={Link} to={item.to} underline="hover" color="primary" sx={{ fontSize: 20, fontWeight: 500 }}>
                {item.label}
              </MuiLink>
            </ListItemText>
          </ListItem>
        ))}
      </List>
    </Paper>
  </Container>
);

export default SiteMap; 