import React, { useState } from 'react';
import {
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  CardMedia,
  IconButton,
  Box,
  Button,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import { useSelector, useDispatch } from 'react-redux';
import { removeFromCart, updateQuantity } from '../store/cartSlice';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';

function Cart() {
  const cartItems = useSelector((state) => state.cart.items);
  const dispatch = useDispatch();
  const [openDialog, setOpenDialog] = useState(false);
  const [orderData, setOrderData] = useState({
    name: '',
    email: '',
    phone: '',
    address: '',
  });
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [snackbarMsg, setSnackbarMsg] = useState('');

  const total = cartItems.reduce((sum, item) => sum + item.price * item.quantity, 0);

  const handleQuantityChange = (id, newQuantity) => {
    if (newQuantity > 0) {
      dispatch(updateQuantity({ id, quantity: newQuantity }));
    }
  };

  const handleRemoveItem = (id) => {
    dispatch(removeFromCart(id));
    setSnackbarMsg('   ');
    setOpenSnackbar(true);
  };

  const handleOrderSubmit = async () => {
    try {
      const response = await fetch('https://nvvideo.ru/api/orders', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          items: cartItems,
          customer: orderData,
          total_amount: total,
        }),
      });

      if (response.ok) {
        const data = await response.json();
        if (data.success && data.payment_url) {
          window.location.href = data.payment_url; //   
          return;
        }
        setOpenDialog(false);
        //     
        cartItems.forEach(item => dispatch(removeFromCart(item.id)));
      }
    } catch (error) {
      console.error('Error submitting order:', error);
    }
  };

  if (cartItems.length === 0) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
           
        </Typography>
        <Snackbar
          open={openSnackbar}
          autoHideDuration={2000}
          onClose={() => setOpenSnackbar(false)}
          anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        >
          <MuiAlert onClose={() => setOpenSnackbar(false)} severity="info" sx={{ width: '100%' }}>
            {snackbarMsg}
          </MuiAlert>
        </Snackbar>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: { xs: 2, md: 4 } }}>
      <Typography variant="h4" component="h1" gutterBottom sx={{ fontSize: { xs: '1.5rem', sm: '2rem' } }}>
        
      </Typography>
      <Grid container spacing={{ xs: 1, md: 3 }}>
        <Grid item xs={12} md={8}>
          {cartItems.map((item) => (
            <Card key={item.id} sx={{ mb: { xs: 1, md: 2 }, flexDirection: { xs: 'column', sm: 'row' }, display: 'flex', alignItems: 'center', p: { xs: 1, md: 2 } }}>
              <CardMedia
                component="img"
                image={item.image_url}
                alt={item.name}
                sx={{ width: { xs: 80, sm: 120 }, height: { xs: 80, sm: 120 }, objectFit: 'contain', borderRadius: 2, mr: { sm: 2, xs: 0 }, mb: { xs: 1, sm: 0 } }}
              />
              <CardContent sx={{ flex: 1, p: { xs: 1, md: 2 } }}>
                <Typography variant="h6" sx={{ fontSize: { xs: '1rem', sm: '1.25rem' } }}>{item.name}</Typography>
                <Typography color="text.secondary" sx={{ fontSize: { xs: '0.9rem', sm: '1rem' } }}>{item.price.toLocaleString('ru-RU')} </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', mt: 1 }}>
                  <TextField
                    type="number"
                    size="small"
                    value={item.quantity}
                    onChange={(e) => handleQuantityChange(item.id, Number(e.target.value))}
                    inputProps={{ min: 1, style: { width: 50, fontSize: 16 } }}
                    sx={{ width: 70, mr: 1 }}
                  />
                  <IconButton color="error" onClick={() => handleRemoveItem(item.id)} size="small">
                    <DeleteIcon fontSize="small" />
                  </IconButton>
                </Box>
              </CardContent>
            </Card>
          ))}
        </Grid>
        <Grid item xs={12} md={4}>
          <Card sx={{ p: { xs: 1, md: 2 } }}>
            <CardContent>
              <Typography variant="h6" gutterBottom sx={{ fontSize: { xs: '1.1rem', sm: '1.25rem' } }}>
                
              </Typography>
              <Typography variant="h4" color="primary" gutterBottom sx={{ fontSize: { xs: '1.5rem', sm: '2rem' } }}>
                {total.toLocaleString('ru-RU')} 
              </Typography>
              <Button
                variant="contained"
                fullWidth
                size="large"
                sx={{ fontSize: { xs: '1rem', sm: '1.1rem' }, py: { xs: 1.2, sm: 1.5 }, borderRadius: 2, mt: 1 }}
                onClick={() => setOpenDialog(true)}
              >
                 
              </Button>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Dialog open={openDialog} onClose={() => setOpenDialog(false)} maxWidth="xs" fullWidth PaperProps={{ sx: { borderRadius: 3, p: { xs: 1, sm: 2 } } }}>
        <DialogTitle sx={{ fontSize: { xs: '1.2rem', sm: '1.5rem' }, textAlign: 'center', pb: 0 }}> </DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 1 }}>
            <TextField
              fullWidth
              label=""
              value={orderData.name}
              onChange={(e) => setOrderData({ ...orderData, name: e.target.value })}
              sx={{ mb: 1.5, fontSize: { xs: '1rem', sm: '1.1rem' } }}
              size="small"
            />
            <TextField
              fullWidth
              label="Email"
              type="email"
              value={orderData.email}
              onChange={(e) => setOrderData({ ...orderData, email: e.target.value })}
              sx={{ mb: 1.5, fontSize: { xs: '1rem', sm: '1.1rem' } }}
              size="small"
            />
            <TextField
              fullWidth
              label=""
              value={orderData.phone}
              onChange={(e) => setOrderData({ ...orderData, phone: e.target.value })}
              sx={{ mb: 1.5, fontSize: { xs: '1rem', sm: '1.1rem' } }}
              size="small"
            />
            <TextField
              fullWidth
              label=" "
              multiline
              rows={2}
              value={orderData.address}
              onChange={(e) => setOrderData({ ...orderData, address: e.target.value })}
              sx={{ fontSize: { xs: '1rem', sm: '1.1rem' } }}
              size="small"
            />
          </Box>
        </DialogContent>
        <DialogActions sx={{ px: 2, pb: 2, flexDirection: { xs: 'column', sm: 'row' }, gap: 1 }}>
          <Button onClick={() => setOpenDialog(false)} variant="outlined" fullWidth sx={{ fontSize: { xs: '1rem', sm: '1.1rem' } }}></Button>
          <Button onClick={handleOrderSubmit} variant="contained" fullWidth sx={{ fontSize: { xs: '1rem', sm: '1.1rem' } }}> </Button>
        </DialogActions>
      </Dialog>

      <Snackbar
        open={openSnackbar}
        autoHideDuration={2000}
        onClose={() => setOpenSnackbar(false)}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <MuiAlert onClose={() => setOpenSnackbar(false)} severity="info" sx={{ width: '100%' }}>
          {snackbarMsg}
        </MuiAlert>
      </Snackbar>
    </Container>
  );
}

export default Cart; 