import React, { useState, useEffect } from 'react';
import { Link as RouterLink, Outlet, useNavigate } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  IconButton,
  Badge,
  Container,
  Box,
  Grid,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Checkbox,
  FormControlLabel,
  Drawer
} from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import StoreIcon from '@mui/icons-material/Store';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import MenuIcon from '@mui/icons-material/Menu';
import { useSelector } from 'react-redux';
import { getCompareList } from '../utils/compare';

const Header = () => {
  const cartItemsCount = useSelector((state) => state.cart.items.length);
  //    
  const [openCallDialog, setOpenCallDialog] = useState(false);
  const [callName, setCallName] = useState('');
  const [callPhone, setCallPhone] = useState('');
  const [callCity, setCallCity] = useState('');
  const [callAgree, setCallAgree] = useState(false);
  const [callError, setCallError] = useState('');
  const [callSuccess, setCallSuccess] = useState(false);

  //     
  const [compareCount, setCompareCount] = useState(0);
  useEffect(() => {
    let mounted = true;
    async function updateCount() {
      const list = await getCompareList();
      if (mounted) setCompareCount(list.length);
    }
    updateCount();
    const onChange = () => updateCount();
    window.addEventListener('compareChanged', onChange);
    window.addEventListener('storage', onChange);
    return () => {
      mounted = false;
      window.removeEventListener('compareChanged', onChange);
      window.removeEventListener('storage', onChange);
    };
  }, []);

  //     
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const navigate = useNavigate();
  const [searchValue, setSearchValue] = useState('');

  const handleCallSubmit = async (e) => {
    e.preventDefault();
    setCallError('');
    setCallSuccess(false);
    if (!callName || !callPhone || !callAgree) {
      setCallError(',        .');
      return;
    }
    try {
      //     API 
      setCallSuccess(true);
      setCallName('');
      setCallPhone('');
      setCallCity('');
      setCallAgree(false);
    } catch {
      setCallError(' .  .');
    }
  };

  const handleSearch = (e) => {
    e.preventDefault();
    if (searchValue.trim()) {
      navigate(`/catalog?search=${encodeURIComponent(searchValue.trim())}`);
    }
  };

  return (
    <>
      <Box sx={{ width: '100%', background: '#fff', borderBottom: '1px solid #e0e0e0', py: { xs: 1, sm: 2 } }}>
        <Container maxWidth="lg" sx={{ px: { xs: 1, sm: 2 } }}>
          <Grid container alignItems="center" spacing={2} direction={{ xs: 'column', sm: 'row' }}>
            <Grid item xs={12} sm={4} sx={{ display: 'flex', alignItems: 'center', justifyContent: { xs: 'center', sm: 'flex-start' }, mb: { xs: 1, sm: 0 } }}>
              <StoreIcon sx={{ color: '#1976d2', fontSize: { xs: 32, sm: 40 }, mr: 1 }} />
              <Box>
                <Typography variant="h6" sx={{ fontWeight: 700, fontSize: { xs: '1.1rem', sm: '1.5rem' }, color: '#1976d2', lineHeight: 1 }}>NVVIDEO</Typography>
                <Typography sx={{ fontSize: { xs: '0.85rem', sm: '1rem' }, color: '#888' }}>     </Typography>
              </Box>
            </Grid>
            <Grid item xs={12} sm={4} sx={{ textAlign: { xs: 'center', sm: 'left' }, mb: { xs: 1, sm: 0 } }}>
              <Typography sx={{ fontWeight: 700, fontSize: { xs: '1.1rem', sm: '1.3rem' }, color: '#222' }}>
                <span style={{ color: '#888', fontWeight: 400, fontSize: '0.95em' }}></span> &nbsp; +7 926 181-01-42
              </Typography>
              <Typography sx={{ fontSize: { xs: '0.85rem', sm: '1rem' }, color: '#888' }}> 9.00-21.00</Typography>
            </Grid>
            <Grid item xs={12} sm={4} sx={{ display: 'flex', flexDirection: 'column', alignItems: { xs: 'center', sm: 'flex-end' }, gap: 0.5 }}>
              <Typography sx={{ fontSize: { xs: '0.9rem', sm: '1rem' }, color: '#1976d2', textAlign: { xs: 'center', sm: 'right' } }}>
                  ?
              </Typography>
              <Typography sx={{ fontWeight: 700, fontSize: { xs: '1rem', sm: '1.1rem' }, color: '#1976d2', textAlign: { xs: 'center', sm: 'right' }, mb: 0.5, cursor: 'pointer' }} onClick={() => setOpenCallDialog(true)}>
                 !
              </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: { xs: 0.5, sm: 1 }, mt: 0.5 }}>
                <IconButton color="default" component={RouterLink} to="/login" size="large" sx={{ p: { xs: 0.5, sm: 1 }, fontSize: { xs: 22, sm: 28 } }}>
                  <AccountCircleIcon sx={{ fontSize: { xs: 26, sm: 32 } }} />
                </IconButton>
                <IconButton color="default" component={RouterLink} to="/compare" size="large" sx={{ p: { xs: 0.5, sm: 1 }, fontSize: { xs: 22, sm: 28 } }}>
                  <Badge badgeContent={compareCount} color="primary">
                    <CompareArrowsIcon sx={{ fontSize: { xs: 22, sm: 28 } }} />
                  </Badge>
                </IconButton>
                <IconButton color="default" component={RouterLink} to="/cart" size="large" sx={{ p: { xs: 0.5, sm: 1 }, fontSize: { xs: 22, sm: 28 } }}>
                  <Badge badgeContent={cartItemsCount} color="primary">
                    <ShoppingCartIcon sx={{ fontSize: { xs: 22, sm: 28 } }} />
                  </Badge>
                </IconButton>
              </Box>
            </Grid>
            <Grid item xs={12} sx={{ width: '100%' }}>
              <form onSubmit={handleSearch} style={{ width: '100%' }}>
                <TextField
                  fullWidth
                  placeholder=""
                  value={searchValue}
                  onChange={e => setSearchValue(e.target.value)}
                  InputProps={{
                    endAdornment: (
                      <IconButton type="submit">
                        <svg width="20" height="20" fill="none"><circle cx="9" cy="9" r="7" stroke="#1976d2" strokeWidth="2"/><path d="M15 15L19 19" stroke="#1976d2" strokeWidth="2" strokeLinecap="round"/></svg>
                      </IconButton>
                    )
                  }}
                  sx={{ maxWidth: 600, mx: 'auto', background: '#fafbfc', borderRadius: 1 }}
                />
              </form>
            </Grid>
          </Grid>
        </Container>
      </Box>
      {/*   Outlet    */}
      <AppBar position="static" sx={{ background: '#0056a6', boxShadow: 'none', minHeight: { xs: 40, sm: 48 } }}>
        <Container maxWidth="lg" disableGutters>
          <Toolbar sx={{ minHeight: { xs: 40, sm: 48 }, px: 0, display: 'flex', justifyContent: 'space-between', width: '100%' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
              {/* -   */}
              <IconButton
                color="inherit"
                edge="start"
                sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }}
                onClick={() => setMobileMenuOpen(true)}
              >
                <MenuIcon />
              </IconButton>
              {/*    */}
              <Typography
                variant="h6"
                component={RouterLink}
                to="/"
                sx={{ color: '#fff', textDecoration: 'none', fontWeight: 700, fontSize: { xs: '1.1rem', sm: '1.5rem' }, flexGrow: { xs: 1, md: 0 } }}
              >
                NVVIDEO
              </Typography>
              {/*   (  xs/sm) */}
              <Box sx={{ display: { xs: 'none', md: 'flex' }, flex: 1, justifyContent: 'center', gap: 2 }}>
                <Button sx={{ color: '#fff', fontWeight: 700 }} component={RouterLink} to="/catalog"></Button>
                <Button sx={{ color: '#fff', fontWeight: 700 }} component={RouterLink} to="/services"></Button>
                <Button sx={{ color: '#fff', fontWeight: 700 }} component={RouterLink} to="/brands"></Button>
                <Button sx={{ color: '#fff', fontWeight: 700 }} component={RouterLink} to="/prices"></Button>
                <Button sx={{ color: '#fff', fontWeight: 700 }} component={RouterLink} to="/company"></Button>
                <Button sx={{ color: '#fff', fontWeight: 700, background: '#ff4c29', borderRadius: 1, px: 3, '&:hover': { background: '#ff6a4d' } }} component={RouterLink} to="/sale">+ </Button>
                <Button sx={{ color: '#fff', fontWeight: 700 }} component={RouterLink} to="/contacts"></Button>
              </Box>
              {/*     */}
              <Box sx={{ display: 'flex', alignItems: 'center', gap: { xs: 0.5, sm: 1 }, minWidth: 0, justifyContent: 'flex-end' }}>
                <IconButton color="default" component={RouterLink} to="/login" size="large" sx={{ p: { xs: 0.5, sm: 1 }, fontSize: { xs: 22, sm: 28 } }}>
                  <AccountCircleIcon sx={{ fontSize: { xs: 26, sm: 32 } }} />
                </IconButton>
                <IconButton color="default" component={RouterLink} to="/compare" size="large" sx={{ p: { xs: 0.5, sm: 1 }, fontSize: { xs: 22, sm: 28 } }}>
                  <Badge badgeContent={compareCount} color="primary">
                    <CompareArrowsIcon sx={{ fontSize: { xs: 22, sm: 28 } }} />
                  </Badge>
                </IconButton>
                <IconButton color="default" component={RouterLink} to="/cart" size="large" sx={{ p: { xs: 0.5, sm: 1 }, fontSize: { xs: 22, sm: 28 } }}>
                  <Badge badgeContent={cartItemsCount} color="primary">
                    <ShoppingCartIcon sx={{ fontSize: { xs: 22, sm: 28 } }} />
                  </Badge>
                </IconButton>
              </Box>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      {/*   */}
      <Drawer anchor="left" open={mobileMenuOpen} onClose={() => setMobileMenuOpen(false)}>
        <Box sx={{ width: 260, p: 2, pt: 3 }}>
          <Button fullWidth sx={{ mb: 1, fontWeight: 700 }} component={RouterLink} to="/catalog" onClick={() => setMobileMenuOpen(false)}></Button>
          <Button fullWidth sx={{ mb: 1, fontWeight: 700 }} component={RouterLink} to="/services" onClick={() => setMobileMenuOpen(false)}></Button>
          <Button fullWidth sx={{ mb: 1, fontWeight: 700 }} component={RouterLink} to="/brands" onClick={() => setMobileMenuOpen(false)}></Button>
          <Button fullWidth sx={{ mb: 1, fontWeight: 700 }} component={RouterLink} to="/prices" onClick={() => setMobileMenuOpen(false)}></Button>
          <Button fullWidth sx={{ mb: 1, fontWeight: 700 }} component={RouterLink} to="/company" onClick={() => setMobileMenuOpen(false)}></Button>
          <Button fullWidth sx={{ mb: 1, fontWeight: 700, background: '#ff4c29', color: '#fff', borderRadius: 1, px: 3, '&:hover': { background: '#ff6a4d' } }} component={RouterLink} to="/sale" onClick={() => setMobileMenuOpen(false)}>+ </Button>
          <Button fullWidth sx={{ mb: 1, fontWeight: 700 }} component={RouterLink} to="/contacts" onClick={() => setMobileMenuOpen(false)}></Button>
        </Box>
      </Drawer>
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <Outlet />
      </Container>
    </>
  );
};

export default Header; 