import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Button, Box, Typography, Table, TableBody, TableCell, TableRow, Paper, Rating, TextField, Grid, Divider, Avatar } from '@mui/material';
import { useDispatch } from 'react-redux';
import { addToCart } from '../store/cartSlice';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';

const ProductDetails = () => {
  const { id } = useParams();
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const dispatch = useDispatch();
  const [reviewText, setReviewText] = useState("");
  const [reviewRating, setReviewRating] = useState(5);
  const [reviews, setReviews] = useState([]);
  const [reviewLoading, setReviewLoading] = useState(false);
  const [openSnackbar, setOpenSnackbar] = useState(false);

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const response = await fetch(`https://nvvideo.ru/api/products/${id}`);
        if (!response.ok) throw new Error('  ');
        const data = await response.json();
        setProduct(data.data);
      } catch (e) {
        setError(e.message);
      } finally {
        setLoading(false);
      }
    };
    fetchProduct();

    //  
    const fetchReviews = async () => {
      try {
        const response = await fetch(`https://nvvideo.ru/api/products/${id}/reviews`);
        if (response.ok) {
          const data = await response.json();
          setReviews(data.data || []);
        }
      } catch {}
    };
    fetchReviews();
  }, [id]);

  const handleAddToCart = () => {
    dispatch(addToCart({ ...product, quantity: 1 }));
    setOpenSnackbar(true);
  };

  const handleBuyNow = () => {
    dispatch(addToCart({ ...product, quantity: 1 }));
    window.location.href = '/cart';
  };

  const handleReviewSubmit = async (e) => {
    e.preventDefault();
    setReviewLoading(true);
    try {
      const response = await fetch(`https://nvvideo.ru/api/products/${id}/reviews`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text: reviewText, rating: reviewRating })
      });
      if (response.ok) {
        setReviewText("");
        setReviewRating(5);
        //  
        const data = await response.json();
        setReviews(data.data || []);
      }
    } finally {
      setReviewLoading(false);
    }
  };

  if (loading) return <div>...</div>;
  if (error) return <div>: {error}</div>;
  if (!product) return <div>  </div>;

  return (
    <Box sx={{ maxWidth: 1100, margin: '0 auto', py: 4 }}>
      <Paper elevation={3} sx={{ p: 3, mb: 4 }}>
        <Grid container spacing={4}>
          <Grid item xs={12} md={5}>
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
              <img src={product.image_url} alt={product.name} style={{ maxWidth: 350, width: '100%', borderRadius: 12, boxShadow: 2, marginBottom: 16 }} />
            </Box>
          </Grid>
          <Grid item xs={12} md={7}>
            <Typography variant="h4" sx={{ mb: 2, fontWeight: 600 }}>{product.name}</Typography>
            <Typography variant="h3" color="primary" sx={{ mb: 2, fontWeight: 700 }}>{product.price.toLocaleString('ru-RU')} </Typography>
            <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
              <Button variant="contained" color="primary" size="large" sx={{ minWidth: 160 }} onClick={handleAddToCart}> </Button>
              <Button variant="contained" color="secondary" size="large" sx={{ minWidth: 160 }} onClick={handleBuyNow}></Button>
            </Box>
            <Divider sx={{ my: 2 }} />
            <Typography variant="body1" sx={{ mb: 2 }}>{product.description}</Typography>
            <Paper variant="outlined" sx={{ p: 2, background: '#fafbfc' }}>
              <Typography variant="h6" sx={{ mb: 1 }}></Typography>
              <Table size="small">
                <TableBody>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 500 }}></TableCell>
                    <TableCell>{product.category_name || product.category_id}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 500 }}></TableCell>
                    <TableCell>{product.stock}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 500 }}></TableCell>
                    <TableCell><Rating value={product.rating} readOnly precision={0.5} /></TableCell>
                  </TableRow>
                  {/*      */}
                </TableBody>
              </Table>
            </Paper>
          </Grid>
        </Grid>
      </Paper>
      {/*  */}
      <Paper elevation={2} sx={{ p: 3, mb: 4 }}>
        <Typography variant="h5" sx={{ mb: 2, fontWeight: 600 }}></Typography>
        {reviews.length === 0 && <Typography>  .</Typography>}
        <Box>
          {reviews.map((review, idx) => (
            <Box key={idx} sx={{ display: 'flex', alignItems: 'flex-start', mb: 3, p: 2, borderRadius: 2, background: '#f7f7fa' }}>
              <Avatar sx={{ mr: 2 }}>{review.user?.name ? review.user.name[0] : 'U'}</Avatar>
              <Box>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 0.5 }}>
                  <Typography variant="subtitle2" sx={{ fontWeight: 500, mr: 1 }}>{review.user?.name || ''}</Typography>
                  <Rating value={review.rating} readOnly precision={0.5} size="small" />
                </Box>
                <Typography variant="body2" color="text.secondary" sx={{ mb: 0.5 }}>{review.text}</Typography>
                {review.created_at && <Typography variant="caption" color="text.disabled">{new Date(review.created_at).toLocaleDateString()}</Typography>}
              </Box>
            </Box>
          ))}
        </Box>
        <Divider sx={{ my: 2 }} />
        <form onSubmit={handleReviewSubmit} style={{ marginTop: 8 }}>
          <Typography variant="subtitle1" sx={{ mb: 1, fontWeight: 500 }}> :</Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
            <Rating
              value={reviewRating}
              onChange={(_, value) => setReviewRating(value)}
              precision={0.5}
              sx={{ mr: 2 }}
            />
            <TextField
              fullWidth
              multiline
              minRows={2}
              value={reviewText}
              onChange={e => setReviewText(e.target.value)}
              placeholder=" "
              sx={{ mr: 2 }}
            />
            <Button type="submit" variant="contained" disabled={reviewLoading || !reviewText} sx={{ minWidth: 120, height: 56 }}></Button>
          </Box>
        </form>
      </Paper>
      <Snackbar
        open={openSnackbar}
        autoHideDuration={2000}
        onClose={() => setOpenSnackbar(false)}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <MuiAlert onClose={() => setOpenSnackbar(false)} severity="success" sx={{ width: '100%' }}>
             !
        </MuiAlert>
      </Snackbar>
    </Box>
  );
};

export default ProductDetails; 