import React, { useEffect, useState } from 'react';
import { Container, Typography, TextField, Button, Box } from '@mui/material';

export default function SiteSettings() {
  const [form, setForm] = useState({
    site_name: '',
    phone: '',
    email: '',
    address: '',
    schedule: '',
  });
  const [loading, setLoading] = useState(true);
  const [success, setSuccess] = useState(false);

  useEffect(() => {
    //   
    fetch('https://nvvideo.ru/api/admin/settings', {
      headers: { 'Authorization': `Bearer ${localStorage.getItem('adminToken')}` }
    })
      .then(res => res.json())
      .then(data => {
        if (data && data.settings) setForm(data.settings);
        setLoading(false);
      });
  }, []);

  const handleChange = e => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = e => {
    e.preventDefault();
    setSuccess(false);
    fetch('https://nvvideo.ru/api/admin/settings', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${localStorage.getItem('adminToken')}`
      },
      body: JSON.stringify(form)
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) setSuccess(true);
      });
  };

  if (loading) return <Container sx={{ my: 8 }}>...</Container>;

  return (
    <Container maxWidth="sm" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
         
      </Typography>
      <Box component="form" onSubmit={handleSubmit}>
        <TextField label=" " name="site_name" value={form.site_name} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
        <TextField label="" name="phone" value={form.phone} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
        <TextField label="Email" name="email" value={form.email} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
        <TextField label="" name="address" value={form.address} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
        <TextField label=" " name="schedule" value={form.schedule} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
        <Button type="submit" variant="contained" color="primary" fullWidth></Button>
        {success && <Typography color="success.main" sx={{ mt: 2 }}> !</Typography>}
      </Box>
    </Container>
  );
} 