//        localStorage
const COMPARE_KEY = 'compareProducts';
const API_URL = 'https://nvvideo.ru/api/compare';

function getToken() {
  return localStorage.getItem('token');
}

// LOCAL
function getLocalList() {
  try {
    return JSON.parse(localStorage.getItem(COMPARE_KEY)) || [];
  } catch {
    return [];
  }
}
function setLocalList(list) {
  localStorage.setItem(COMPARE_KEY, JSON.stringify(list));
}

// SERVER
async function getServerList() {
  const token = getToken();
  try {
    const res = await fetch(API_URL, {
      headers: { Authorization: `Bearer ${token}` }
    });
    const data = await res.json();
    return data.data || [];
  } catch {
    return [];
  }
}
async function addServer(product) {
  const token = getToken();
  try {
    await fetch(API_URL, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${token}` },
      body: JSON.stringify({ product_id: product.id })
    });
  } catch {}
}
async function removeServer(productId) {
  const token = getToken();
  try {
    await fetch(`${API_URL}/${productId}`, {
      method: 'DELETE',
      headers: { Authorization: `Bearer ${token}` }
    });
  } catch {}
}
async function clearServer() {
  const token = getToken();
  try {
    await fetch(API_URL, {
      method: 'DELETE',
      headers: { Authorization: `Bearer ${token}` }
    });
  } catch {}
}

// EXPORT API
export async function getCompareList() {
  if (getToken()) return await getServerList();
  return getLocalList();
}
export async function addToCompare(product) {
  if (getToken()) return await addServer(product);
  const list = getLocalList();
  if (!list.find((p) => p.id === product.id)) {
    list.push(product);
    setLocalList(list);
  }
}
export async function removeFromCompare(productId) {
  if (getToken()) return await removeServer(productId);
  const list = getLocalList().filter((p) => p.id !== productId);
  setLocalList(list);
}
export async function clearCompare() {
  if (getToken()) return await clearServer();
  setLocalList([]);
}
export async function isInCompare(productId) {
  const list = await getCompareList();
  return list.some((p) => p.id === productId);
} 