import React, { useState } from 'react';
import { Container, Typography, TextField, Button, Box } from '@mui/material';

export default function Contacts() {
  const [form, setForm] = useState({ name: '', email: '', message: '' });
  const [sent, setSent] = useState(false);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setSent(true);
    //      
  };

  return (
    <Container maxWidth="sm" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        
      </Typography>
      <Typography variant="body1" sx={{ mb: 2 }}>
        : .. - . 19, .  ,  17b
        <br />: +7 926 181-01-42
        <br />Email: nvvideo.ru@gmail.com
      </Typography>
      <Box component="form" onSubmit={handleSubmit} sx={{ mt: 4, maxWidth: 400, mx: 'auto', p: { xs: 2, sm: 4 }, boxShadow: 2, borderRadius: 2 }}>
        <Typography variant="h6" sx={{ mb: 2, fontSize: { xs: '1.1rem', sm: '1.3rem' } }}> </Typography>
        <TextField
          label=" "
          name="name"
          value={form.name}
          onChange={handleChange}
          fullWidth
          sx={{ mb: 2 }}
          size="large"
        />
        <TextField
          label="Email"
          name="email"
          value={form.email}
          onChange={handleChange}
          fullWidth
          sx={{ mb: 2 }}
          size="large"
        />
        <TextField
          label=""
          name="message"
          value={form.message}
          onChange={handleChange}
          fullWidth
          multiline
          rows={4}
          sx={{ mb: 2 }}
          size="large"
        />
        <Button type="submit" variant="contained" color="primary" fullWidth size="large" sx={{ fontSize: { xs: '1rem', sm: '1.1rem' }, py: 1.5 }}>
          
        </Button>
        {sent && <Typography color="success.main" sx={{ mt: 2 }}>!   .</Typography>}
      </Box>
    </Container>
  );
} 