import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material';
import { Provider } from 'react-redux';
import { store } from './store';
import Header from './components/Header';
import Catalog from './components/Catalog';
import Cart from './components/Cart';
import Login from './components/Login';
import Register from './components/Register';
import AdminLogin from './components/admin/AdminLogin';
import AdminLayout from './components/admin/AdminLayout';
import ProductManagement from './components/admin/ProductManagement';
import CategoryManagement from './components/admin/CategoryManagement';
import ProductDetails from './pages/ProductDetails';
import Home from './pages/Home';
import SiteMap from './pages/SiteMap';
import Services from './pages/Services';
import Brands from './pages/Brands';
import Prices from './pages/Prices';
import Company from './pages/Company';
import Sale from './pages/Sale';
import Contacts from './pages/Contacts';
import SiteSettings from './components/admin/SiteSettings';
import Footer from './components/Footer';
import News from './pages/News';
import Delivery from './pages/Delivery';
import Return from './pages/Return';
import Faq from './pages/Faq';
import Franchise from './pages/Franchise';
import Compare from './pages/Compare';
import Privacy from './pages/Privacy';
import Offer from './pages/Offer';
import Payment from './pages/Payment';
import PaymentSuccess from './pages/PaymentSuccess';
import PaymentFail from './pages/PaymentFail';

const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
  typography: {
    fontFamily: [
      'Roboto',
      'Arial',
      'sans-serif',
    ].join(','),
    h1: {
      fontSize: '2.5rem',
      '@media (max-width:600px)': {
        fontSize: '2rem',
      },
    },
    h2: {
      fontSize: '2rem',
      '@media (max-width:600px)': {
        fontSize: '1.75rem',
      },
    },
    h3: {
      fontSize: '1.75rem',
      '@media (max-width:600px)': {
        fontSize: '1.5rem',
      },
    },
    body1: {
      fontSize: '1rem',
      '@media (max-width:600px)': {
        fontSize: '0.875rem',
      },
    },
  },
  components: {
    MuiContainer: {
      styleOverrides: {
        root: {
          paddingLeft: {
            xs: 2,
            sm: 3,
            md: 4,
          },
          paddingRight: {
            xs: 2,
            sm: 3,
            md: 4,
          },
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          '@media (max-width:600px)': {
            padding: '6px 12px',
            fontSize: '0.875rem',
          },
        },
      },
    },
    MuiCard: {
      styleOverrides: {
        root: {
          '@media (max-width:600px)': {
            margin: '8px',
          },
        },
      },
    },
  },
  breakpoints: {
    values: {
      xs: 0,
      sm: 600,
      md: 960,
      lg: 1280,
      xl: 1920,
    },
  },
});

//    -
const ProtectedRoute = ({ children }) => {
  const isAuthenticated = localStorage.getItem('adminToken');
  return isAuthenticated ? children : <Navigate to="/admin/login" />;
};

function App() {
  return (
    <Provider store={store}>
      <ThemeProvider theme={theme}>
        <Router>
          <Routes>
            {/*   */}
            <Route element={<Header />}>
              <Route path="/" element={<><Home /><Footer /></>} />
              <Route path="/catalog" element={<><Catalog /><Footer /></>} />
              <Route path="/cart" element={<><Cart /><Footer /></>} />
              <Route path="/login" element={<><Login /><Footer /></>} />
              <Route path="/register" element={<><Register /><Footer /></>} />
              <Route path="/product/:id" element={<><ProductDetails /><Footer /></>} />
              <Route path="/sitemap" element={<><SiteMap /><Footer /></>} />
              <Route path="/services" element={<><Services /><Footer /></>} />
              <Route path="/brands" element={<><Brands /><Footer /></>} />
              <Route path="/prices" element={<><Prices /><Footer /></>} />
              <Route path="/company" element={<><Company /><Footer /></>} />
              <Route path="/sale" element={<><Sale /><Footer /></>} />
              <Route path="/contacts" element={<><Contacts /><Footer /></>} />
              <Route path="/news" element={<><News /><Footer /></>} />
              <Route path="/delivery" element={<><Delivery /><Footer /></>} />
              <Route path="/return" element={<><Return /><Footer /></>} />
              <Route path="/faq" element={<><Faq /><Footer /></>} />
              <Route path="/franchise" element={<><Franchise /><Footer /></>} />
              <Route path="/compare" element={<><Compare /><Footer /></>} />
              <Route path="/privacy" element={<Privacy />} />
              <Route path="/offer" element={<Offer />} />
              <Route path="/payment/:orderId" element={<Payment />} />
              <Route path="/payment/success/:orderId" element={<PaymentSuccess />} />
              <Route path="/payment/success" element={<PaymentSuccess />} />
              <Route path="/payment/fail" element={<PaymentFail />} />
            </Route>

            {/* - */}
            <Route path="/admin/login" element={<AdminLogin />} />
            <Route
              path="/admin"
              element={
                <ProtectedRoute>
                  <AdminLayout />
                </ProtectedRoute>
              }
            >
              <Route index element={<Navigate to="/admin/products" />} />
              <Route path="products" element={<ProductManagement />} />
              <Route path="categories" element={<CategoryManagement />} />
              <Route path="settings" element={<SiteSettings />} />
            </Route>
          </Routes>
        </Router>
      </ThemeProvider>
    </Provider>
  );
}

export default App; 