import React, { useState, useEffect, useCallback } from 'react';
import { useSearchParams } from 'react-router-dom';
import {
  Container,
  Grid,
  Typography,
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Slider,
  Paper,
  Button,
  CircularProgress,
  Alert,
} from '@mui/material';
import ProductCard from './ProductCard';

const Catalog = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [filters, setFilters] = useState({
    category: searchParams.get('category') || '',
    search: searchParams.get('search') || '',
    priceRange: [0, 100000],
  });
  const [draftFilters, setDraftFilters] = useState({
    category: searchParams.get('category') || '',
    search: searchParams.get('search') || '',
    priceRange: [0, 100000],
  });

  const filterProducts = useCallback(() => {
    let filtered = [...products];

    if (filters.search) {
      const searchLower = filters.search.toLowerCase();
      filtered = filtered.filter(product =>
        product.name.toLowerCase().includes(searchLower) ||
        product.description.toLowerCase().includes(searchLower)
      );
    }

    if (filters.category) {
      filtered = filtered.filter(product => String(product.category_id) === String(filters.category));
    }

    filtered = filtered.filter(product =>
      product.price >= filters.priceRange[0] &&
      product.price <= filters.priceRange[1]
    );

    setFilteredProducts(filtered);
  }, [products, filters]);

  useEffect(() => {
    const category = searchParams.get('category');
    const search = searchParams.get('search');
    if (category || search) {
      setFilters(prev => ({
        ...prev,
        category: category || '',
        search: search || '',
      }));
      setDraftFilters(prev => ({
        ...prev,
        category: category || '',
        search: search || '',
      }));
    }
  }, [searchParams]);

  useEffect(() => {
    fetchProducts();
    fetchCategories();
  }, []);

  useEffect(() => {
    filterProducts();
  }, [filters, filterProducts]);

  const fetchProducts = async () => {
    try {
      setLoading(true);
      const response = await fetch('https://nvvideo.ru/api/products');
      if (!response.ok) {
        throw new Error('Failed to fetch products');
      }
      const data = await response.json();
      setProducts(data.data || []);
      setFilteredProducts(data.data || []);
      setError(null);
    } catch (error) {
      console.error('Error fetching products:', error);
      setError('   . ,  .');
    } finally {
      setLoading(false);
    }
  };

  const fetchCategories = async () => {
    try {
      const response = await fetch('https://nvvideo.ru/api/categories');
      if (!response.ok) {
        throw new Error('Failed to fetch categories');
      }
      const data = await response.json();
      setCategories(data);
    } catch (error) {
      console.error('Error fetching categories:', error);
    }
  };

  const handleDraftFilterChange = (name, value) => {
    setDraftFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleApplyFilters = () => {
    const newFilters = { ...draftFilters };
    setFilters(newFilters);
    
    //  URL    
    const params = new URLSearchParams();
    if (newFilters.category) params.set('category', newFilters.category);
    if (newFilters.search) params.set('search', newFilters.search);
    setSearchParams(params);
  };

  const handleResetFilters = () => {
    setDraftFilters({
      category: '',
      search: '',
      priceRange: [0, 100000],
    });
    setFilters({
      category: '',
      search: '',
      priceRange: [0, 100000],
    });
    setSearchParams({});
  };

  return (
    <Container maxWidth="lg" sx={{ py: { xs: 4, sm: 6 }, minHeight: '80vh' }}>
      <Typography 
        variant="h3" 
        component="h1" 
        gutterBottom
        sx={{
          fontWeight: 800,
          fontSize: { xs: '2rem', sm: '2.5rem', md: '3rem' },
          textAlign: { xs: 'center', sm: 'left' },
          letterSpacing: 1,
          mb: 2,
          color: 'primary.main',
          textShadow: '0 2px 8px rgba(25,118,210,0.08)'
        }}
      >
        
      </Typography>
      {/*   */}
      {categories.length > 0 && (
        <Grid container spacing={2} sx={{ mb: 4 }}>
          {categories.filter(cat => !!cat.image_url).map((cat) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={cat.id}>
              <Paper elevation={1} sx={{ p: 3, textAlign: 'center', height: 220, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', borderRadius: 3, transition: 'box-shadow 0.2s', '&:hover': { boxShadow: 6 } }}>
                <Box sx={{ mb: 2 }}>
                  <img src={cat.image_url} alt={cat.name} style={{ maxHeight: 64, maxWidth: '100%' }} />
                </Box>
                <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 0.5 }}>{cat.name}</Typography>
                <Typography variant="body2" color="text.secondary">{cat.products_count ? `${cat.products_count} ` : ''}</Typography>
              </Paper>
            </Grid>
          ))}
        </Grid>
      )}
      <Grid container spacing={{ xs: 2, md: 4 }} alignItems="flex-start">
        <Grid item xs={12} md={3}>
          <Paper 
            elevation={4}
            sx={{ 
              p: { xs: 2, sm: 3 },
              borderRadius: 4,
              boxShadow: '0 4px 24px 0 rgba(25,118,210,0.08)',
              position: { xs: 'sticky', sm: 'static' },
              top: { xs: 0 },
              zIndex: { xs: 1000 },
              mb: { xs: 2, sm: 0 },
              background: '#fff',
              minWidth: 260
            }}
          >
            <Typography 
              variant="h6" 
              gutterBottom
              sx={{
                fontSize: { xs: '1.2rem', sm: '1.35rem' },
                fontWeight: 700,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                mb: 2,
                color: 'primary.main',
                letterSpacing: 0.5
              }}
            >
              
              <Button
                variant="outlined"
                size="small"
                onClick={handleResetFilters}
                sx={{ 
                  display: { xs: 'block', sm: 'none' },
                  minWidth: 'auto',
                  px: 1
                }}
              >
                
              </Button>
            </Typography>

            <Box sx={{ mb: 3 }}>
              <TextField
                fullWidth
                label=""
                value={draftFilters.search}
                onChange={(e) => handleDraftFilterChange('search', e.target.value)}
                sx={{ 
                  mb: 2,
                  '& .MuiInputBase-root': {
                    height: { xs: '44px', sm: '56px' },
                    borderRadius: 2
                  }
                }}
              />

              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel></InputLabel>
                <Select
                  value={draftFilters.category}
                  label=""
                  onChange={(e) => handleDraftFilterChange('category', e.target.value)}
                  sx={{
                    '& .MuiSelect-select': {
                      py: { xs: 1, sm: 1.5 }
                    }
                  }}
                >
                  <MenuItem value=""> </MenuItem>
                  {categories.map((category) => (
                    <MenuItem key={category.id} value={category.id}>
                      {category.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <Typography 
                gutterBottom
                sx={{
                  fontSize: { xs: '0.875rem', sm: '1rem' }
                }}
              >
                
              </Typography>
              <Slider
                value={draftFilters.priceRange}
                onChange={(_, value) => handleDraftFilterChange('priceRange', value)}
                valueLabelDisplay="auto"
                min={0}
                max={100000}
                step={1000}
                valueLabelFormat={(value) => `${value.toLocaleString('ru-RU')} `}
                sx={{
                  '& .MuiSlider-thumb': {
                    width: { xs: 16, sm: 20 },
                    height: { xs: 16, sm: 20 }
                  }
                }}
              />
              <Box sx={{ 
                mt: 2, 
                display: 'flex', 
                gap: 1,
                flexDirection: { xs: 'column', sm: 'row' }
              }}>
                <Button
                  variant="contained"
                  onClick={handleApplyFilters}
                  fullWidth
                  sx={{
                    py: { xs: 1.2, sm: 1.5 },
                    fontWeight: 700,
                    fontSize: { xs: '1rem', sm: '1.1rem' },
                    borderRadius: 2,
                    boxShadow: '0 2px 8px 0 rgba(25,118,210,0.08)'
                  }}
                >
                  
                </Button>
                <Button
                  variant="outlined"
                  onClick={handleResetFilters}
                  fullWidth
                  sx={{
                    display: { xs: 'none', sm: 'block' },
                    py: { xs: 1.2, sm: 1.5 },
                    fontWeight: 700,
                    fontSize: { xs: '1rem', sm: '1.1rem' },
                    borderRadius: 2
                  }}
                >
                  
                </Button>
              </Box>
            </Box>
          </Paper>
        </Grid>

        <Grid item xs={12} md={9}>
          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
              <CircularProgress />
            </Box>
          ) : error ? (
            <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>
          ) : filteredProducts.length === 0 ? (
            <Alert severity="info" sx={{ mb: 2 }}>
                .    .
            </Alert>
          ) : (
            <Grid container spacing={{ xs: 3, sm: 4 }}>
              {filteredProducts.map((product) => (
                <Grid item xs={12} sm={6} md={4} key={product.id}>
                  <Box sx={{ height: '100%' }}>
                    <ProductCard product={product} />
                  </Box>
                </Grid>
              ))}
            </Grid>
          )}
        </Grid>
      </Grid>
    </Container>
  );
}

export default Catalog; 