import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  Button,
  CircularProgress,
  Alert,
  Divider,
} from '@mui/material';
import axios from 'axios';

const paymentMethods = [
  {
    id: 'card',
    label: '  ',
    description: '  Visa, MasterCard,   FreeKassa',
  },
  {
    id: 'cash',
    label: '  ',
    description: '      ',
  },
  {
    id: 'invoice',
    label: '   . ',
    description: '   ',
  },
];

export default function Payment() {
  const { orderId } = useParams();
  const navigate = useNavigate();
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedMethod, setSelectedMethod] = useState('card');
  const [processing, setProcessing] = useState(false);

  useEffect(() => {
    const fetchOrder = async () => {
      try {
        const response = await axios.get(`/api/orders/${orderId}`);
        setOrder(response.data);
      } catch (err) {
        setError('     ');
      } finally {
        setLoading(false);
      }
    };

    fetchOrder();
  }, [orderId]);

  const handlePayment = async () => {
    setProcessing(true);
    try {
      const response = await axios.post(`/api/orders/${orderId}/payment`, {
        method: selectedMethod,
      });

      if (selectedMethod === 'card') {
        //     FreeKassa
        window.location.href = response.data.paymentUrl;
      } else {
        //       
        navigate(`/payment/success/${orderId}`);
      }
    } catch (err) {
      setError('    ');
    } finally {
      setProcessing(false);
    }
  };

  if (loading) {
    return (
      <Container maxWidth="md" sx={{ my: 4, textAlign: 'center' }}>
        <CircularProgress />
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="md" sx={{ my: 4 }}>
        <Alert severity="error">{error}</Alert>
      </Container>
    );
  }

  if (!order) {
    return (
      <Container maxWidth="md" sx={{ my: 4 }}>
        <Alert severity="error">  </Alert>
      </Container>
    );
  }

  return (
    <Container maxWidth="md" sx={{ my: 4 }}>
      <Typography variant="h4" sx={{ mb: 3, fontWeight: 600, fontSize: { xs: '1.2rem', sm: '2rem' } }}>
          #{order.id}
      </Typography>
      <Paper elevation={3} sx={{ p: { xs: 2, sm: 3 }, mb: 3 }}>
        <Typography variant="h6" sx={{ mb: 2, fontSize: { xs: '1rem', sm: '1.2rem' } }}>
            
        </Typography>
        <Box sx={{ mb: 2 }}>
          <Typography variant="body1" sx={{ fontSize: { xs: '1rem', sm: '1.1rem' } }}>
              : <strong>{order.total.toLocaleString('ru-RU')} </strong>
          </Typography>
        </Box>
        <Divider sx={{ my: 2 }} />
        <Typography variant="h6" sx={{ mb: 2, fontSize: { xs: '1rem', sm: '1.2rem' } }}>
            
        </Typography>
        <FormControl component="fieldset" fullWidth>
          <RadioGroup
            value={selectedMethod}
            onChange={(e) => setSelectedMethod(e.target.value)}
            sx={{ flexDirection: 'column' }}
          >
            {paymentMethods.map((method) => (
              <FormControlLabel
                key={method.id}
                value={method.id}
                control={<Radio />}
                label={
                  <Box>
                    <Typography variant="body1" sx={{ fontWeight: 500, fontSize: { xs: '1rem', sm: '1.1rem' } }}>
                      {method.label}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ fontSize: { xs: '0.95rem', sm: '1rem' } }}>
                      {method.description}
                    </Typography>
                  </Box>
                }
                sx={{ mb: 2 }}
              />
            ))}
          </RadioGroup>
        </FormControl>
      </Paper>
      <Box sx={{ display: 'flex', flexDirection: { xs: 'column', sm: 'row' }, gap: 2 }}>
        <Button
          variant="outlined"
          onClick={() => navigate(-1)}
          disabled={processing}
          fullWidth={true}
          sx={{ fontSize: { xs: '1rem', sm: '1.1rem' }, py: { xs: 1.2, sm: 1.5 } }}
        >
          
        </Button>
        <Button
          variant="contained"
          onClick={handlePayment}
          disabled={processing}
          fullWidth={true}
          sx={{ fontSize: { xs: '1rem', sm: '1.1rem' }, py: { xs: 1.2, sm: 1.5 } }}
        >
          {processing ? <CircularProgress size={24} /> : ''}
        </Button>
      </Box>
    </Container>
  );
} 