import React from 'react';
import {
  Card,
  CardContent,
  CardMedia,
  Typography,
  Button,
  Box,
  Rating,
  Snackbar,
  Alert,
  IconButton,
} from '@mui/material';
import { useDispatch } from 'react-redux';
import { addToCart } from '../store/cartSlice';
import { Link } from 'react-router-dom';
import { addToCompare, removeFromCompare, isInCompare } from '../utils/compare';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';

function ProductCard({ product }) {
  const dispatch = useDispatch();
  const [openSnackbar, setOpenSnackbar] = React.useState(false);
  const [inCompare, setInCompare] = React.useState(false);

  React.useEffect(() => {
    let mounted = true;
    (async () => {
      const res = await isInCompare(product.id);
      if (mounted) setInCompare(res);
    })();
    return () => { mounted = false; };
  }, [product.id]);

  const handleAddToCart = () => {
    dispatch(addToCart({ ...product, quantity: 1 }));
    setOpenSnackbar(true);
  };

  const handleCompareClick = async () => {
    if (inCompare) {
      await removeFromCompare(product.id);
      setInCompare(false);
    } else {
      await addToCompare(product);
      setInCompare(true);
    }
    window.dispatchEvent(new Event('compareChanged'));
  };

  return (
    <Card 
      sx={{ 
        height: '100%', 
        display: 'flex', 
        flexDirection: 'column',
        transition: 'transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out',
        '&:hover': {
          transform: 'translateY(-4px)',
          boxShadow: '0 4px 20px rgba(0,0,0,0.1)'
        },
        p: { xs: 1, sm: 2 }
      }}
    >
      <CardMedia
        component="img"
        image={product.image}
        alt={product.name}
        sx={{ objectFit: 'contain', width: '100%', height: { xs: 160, sm: 200, md: 220 } }}
      />
      <CardContent sx={{ flexGrow: 1, p: { xs: 1, sm: 2 } }}>
        <Typography gutterBottom variant="h6" component="div" sx={{ fontSize: { xs: '1rem', sm: '1.2rem' } }}>
          <Link 
            to={`/product/${product.id}`} 
            style={{ 
              textDecoration: 'none', 
              color: 'inherit',
              display: '-webkit-box',
              WebkitLineClamp: 2,
              WebkitBoxOrient: 'vertical',
              overflow: 'hidden',
              textOverflow: 'ellipsis'
            }}
          >
            {product.name}
          </Link>
        </Typography>
        <Typography variant="body2" color="text.secondary" sx={{ fontSize: { xs: '0.95rem', sm: '1rem' } }}>
          {product.description}
        </Typography>
        <Box 
          sx={{ 
            display: 'flex', 
            alignItems: 'center', 
            mb: { xs: 0.5, sm: 1 }
          }}
        >
          <Rating 
            value={product.rating} 
            readOnly 
            precision={0.5}
            size="small"
            sx={{
              '& .MuiRating-iconFilled': {
                color: 'primary.main'
              }
            }}
          />
          <Typography 
            variant="body2" 
            color="text.secondary" 
            sx={{ 
              ml: 1,
              fontSize: { xs: '0.75rem', sm: '0.875rem' }
            }}
          >
            ({product.reviews_count})
          </Typography>
        </Box>
      </CardContent>
      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', px: { xs: 1, sm: 2 }, pb: { xs: 1, sm: 2 } }}>
        <Typography variant="h6" sx={{ fontWeight: 700, fontSize: { xs: '1.1rem', sm: '1.3rem' } }}>{product.price.toLocaleString('ru-RU')} </Typography>
        <Button variant="contained" size="large" fullWidth sx={{ ml: 2, fontSize: { xs: '1rem', sm: '1.1rem' }, py: { xs: 1, sm: 1.5 } }} onClick={handleAddToCart}>
           
        </Button>
      </Box>
      <Box 
        sx={{ 
          display: 'flex', 
          gap: { xs: 0.5, sm: 1 }, 
          mt: { xs: 1, sm: 2 }
        }}
      >
        <Button
          variant="contained"
          component={Link}
          to={`/product/${product.id}`}
          sx={{ 
            flex: 1,
            py: { xs: 0.75, sm: 1 },
            fontSize: { xs: '0.75rem', sm: '0.875rem' }
          }}
        >
          
        </Button>
        <IconButton
          color={inCompare ? 'primary' : 'default'}
          onClick={handleCompareClick}
          sx={{ ml: 1 }}
          title={inCompare ? '  ' : ' '}
        >
          <CompareArrowsIcon />
        </IconButton>
      </Box>
      <Snackbar
        open={openSnackbar}
        autoHideDuration={2000}
        onClose={() => setOpenSnackbar(false)}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Alert 
          onClose={() => setOpenSnackbar(false)} 
          severity="success" 
          sx={{ 
            width: '100%',
            fontSize: { xs: '0.875rem', sm: '1rem' }
          }}
        >
             !
        </Alert>
      </Snackbar>
    </Card>
  );
}

export default ProductCard; 