import React, { useEffect } from 'react';
import { Box, Typography, Button, Grid, Paper, Container } from '@mui/material';
import { Link } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import { Navigation, Pagination, Autoplay } from 'swiper/modules';

const slides = [
  {
    image: 'https://rvn-group.ru/upload/iblock/57a/orfireu1ltlujui2tz5pimk6rm7jwefw/yv3sab9rzjl0x3ixl0yhtu76zjf0k4e6.png',
    title: 'NVVIDEO   iFLOW',
    subtitle: '',
    button: { text: '', link: '/catalog' },
    bg: 'linear-gradient(90deg, #2196f3 0%, #e3f2fd 100%)',
  },
  {
    image: 'https://rvn-group.ru/upload/iblock/5ff/zmcyzmm3i7eiruin6ckjafne0v45n6ww/collage-_4_.png',
    title: '! SD- Unibrave',
    subtitle: '',
    button: { text: '', link: '/catalog' },
    bg: 'linear-gradient(90deg, #388e3c 0%, #e8f5e9 100%)',
  },
  {
    image: '/images/slide3.jpg',
    title: '   ',
    subtitle: '',
    button: { text: '', link: '/catalog' },
    bg: 'linear-gradient(90deg, #ff9800 0%, #fff3e0 100%)',
  },
];

const categories = [
  {
    name: '',
    image: 'https://cdn-icons-png.flaticon.com/512/3062/3062634.png',
    link: '/catalog?category=1'
  },
  {
    name: '  ',
    image: 'https://cdn-icons-png.flaticon.com/512/1048/1048953.png',
    link: '/catalog?category=2'
  },
  {
    name: ' ',
    image: 'https://cdn-icons-png.flaticon.com/512/1048/1048927.png',
    link: '/catalog?category=3'
  },
  {
    name: '',
    image: 'https://cdn-icons-png.flaticon.com/512/1048/1048916.png',
    link: '/catalog?category=4'
  },
];

const Home = () => {
  useEffect(() => {
    function initMap() {
      const mapContainer = document.getElementById('map');
      if (!mapContainer || !window.ymaps) return;
      mapContainer.innerHTML = '';
      try {
        const map = new window.ymaps.Map('map', {
          center: [55.759635, 37.864895],
          zoom: 17,
          controls: ['zoomControl', 'fullscreenControl']
        });
        const placemark = new window.ymaps.Placemark([55.759635, 37.864895], {
          balloonContent: 'NVVIDEO -  ,  17b',
          hintContent: 'NVVIDEO'
        }, {
          preset: 'islands#blueStretchyIcon'
        });
        map.geoObjects.add(placemark);
        map.behaviors.disable('scrollZoom');
      } catch (e) {
        // eslint-disable-next-line no-console
        console.error('  :', e);
      }
    }
    if (!document.getElementById('yandex-maps-script')) {
      const script = document.createElement('script');
      script.id = 'yandex-maps-script';
      script.src = 'https://api-maps.yandex.ru/2.1/?apikey=a01a98b5-7ba4-412c-8153-314eaa9f49c8&lang=ru_RU';
      script.async = true;
      document.body.appendChild(script);
      script.onload = () => {
        if (window.ymaps) {
          window.ymaps.ready(initMap);
        }
      };
    } else {
      if (window.ymaps) {
        window.ymaps.ready(initMap);
      }
    }
    return () => {
      const mapContainer = document.getElementById('map');
      if (mapContainer) mapContainer.innerHTML = '';
    };
  }, []);

  return (
    <Box sx={{ background: '#f5f7fa' }}>
      {/*  */}
      <Box sx={{ width: '100%', background: '#fff', mb: 4 }}>
        <Swiper
          modules={[Navigation, Pagination, Autoplay]}
          navigation
          pagination={{ clickable: true }}
          autoplay={{ delay: 5000, disableOnInteraction: false }}
          loop
          style={{ width: '100%', height: 420 }}
        >
          {slides.map((slide, idx) => (
            <SwiperSlide key={idx}>
              <Box sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                height: 420,
                background: slide.bg,
                px: 6,
              }}>
                <Grid container alignItems="center" spacing={4}>
                  <Grid item xs={12} md={6}>
                    <Typography variant="h3" sx={{ fontWeight: 700, mb: 2, color: '#222' }}>{slide.title}</Typography>
                    {slide.subtitle && <Typography variant="h6" sx={{ mb: 3 }}>{slide.subtitle}</Typography>}
                    <Button variant="contained" color="primary" size="large" component={Link} to={slide.button.link} sx={{ mt: 2 }}>
                      {slide.button.text}
                    </Button>
                  </Grid>
                  <Grid item xs={12} md={6} sx={{ textAlign: 'center' }}>
                    <img src={slide.image} alt={slide.title} style={{ maxHeight: 260, maxWidth: '100%' }} />
                  </Grid>
                </Grid>
              </Box>
            </SwiperSlide>
          ))}
        </Swiper>
      </Box>
      {/*  "   " */}
      <Container maxWidth="lg" sx={{ mb: 4 }}>
        <Typography variant="h4" sx={{ fontWeight: 600, mb: 2, textAlign: 'center' }}>
             
        </Typography>
        <Typography variant="h6" color="text.secondary" sx={{ mb: 4, textAlign: 'center' }}>
           
        </Typography>
        <Grid container spacing={3} justifyContent="center">
          {categories.map((cat, idx) => (
            <Grid item xs={12} sm={6} md={3} key={idx}>
              <Paper elevation={2} sx={{ p: 2, textAlign: 'center', height: 200, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', background: '#fff' }}>
                <img src={cat.image} alt={cat.name} style={{ maxHeight: 70, marginBottom: 16 }} />
                <Typography variant="h6" sx={{ mb: 1 }}>{cat.name}</Typography>
                <Button variant="outlined" size="small" component={Link} to={cat.link}></Button>
              </Paper>
            </Grid>
          ))}
        </Grid>
        <Box sx={{ textAlign: 'center', mt: 4 }}>
          <Button variant="contained" color="primary" size="large" component={Link} to="/catalog">
             
          </Button>
        </Box>
      </Container>
      {/*     */}
      <Container maxWidth="lg" sx={{ mb: 4 }}>
        <Paper elevation={3} sx={{ p: 4, textAlign: 'center', borderRadius: 3, background: 'linear-gradient(90deg, #e3f2fd 0%, #fff 100%)' }}>
          <Typography variant="h5" sx={{ fontWeight: 700, mb: 1, color: '#1976d2', letterSpacing: 0.5 }}>
              NVVIDEO
          </Typography>
          <Typography variant="h6" sx={{ fontWeight: 500, color: '#333', mb: 1 }}>
            .. - . 19, 
          </Typography>
          <Typography variant="body1" sx={{ color: '#555', mb: 2 }}>
             ,  17b
          </Typography>
          <Box id="map" style={{ width: '100%', height: '400px', marginTop: '20px' }}></Box>
        </Paper>
      </Container>
    </Box>
  );
};

export default Home; 