import React from 'react';
import { Box, Container, Grid, Typography, Link, List, ListItem, ListItemButton, ListItemText } from '@mui/material';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';

const catalogLinks = [
  { label: '', to: '/catalog?category=1' },
  { label: '    ', to: '/catalog?category=2' },
  { label: '   ', to: '/catalog?category=13' },
  { label: '- ', to: '/catalog?category=14' },
  { label: ' ', to: '/catalog?category=15' },
  { label: ' ', to: '/catalog?category=16' },
  { label: ' ', to: '/catalog?category=17' },
  { label: ' ', to: '/catalog?category=18' },
  { label: '   ', to: '/catalog?category=19' },
  { label: '   ', to: '/catalog?category=20' },
  { label: '', to: '/catalog?category=21' },
  { label: '    ', to: '/catalog?category=22' },
  { label: ' ', to: '/catalog?category=23' },
  { label: ' ', to: '/catalog?category=24' },
  { label: ' ', to: '/catalog?category=25' },
];

const infoLinks = [
  { label: ' ', to: '/company' },
  { label: '', to: '/news' },
  { label: ' ', to: '/contacts' },
  { label: '  ', to: '/delivery' },
  { label: '', to: '/sale' },
  { label: '  ', to: '/return' },
  { label: '-', to: '/faq' },
  { label: '', to: '/franchise' },
  { label: '', to: '/sale' },
  { label: ' ', to: '/offer', highlight: true },
  { label: ' ', to: '/privacy' },
];

export default function Footer() {
  return (
    <Box
      component="footer"
      sx={{
        bgcolor: '#393939',
        color: 'white',
        py: { xs: 4, sm: 6 },
        fontSize: { xs: '14px', sm: '15px' },
        width: '100vw',
        marginX: 'calc(50% - 50vw)',
      }}
    >
      <Container maxWidth="lg" sx={{ py: { xs: 2, md: 4 } }}>
        <Grid container spacing={2} direction={{ xs: 'column', md: 'row' }}>
          <Grid item xs={12} md={4}>
            <Typography 
              variant="subtitle1" 
              sx={{ 
                mb: { xs: 1, sm: 2 }, 
                textTransform: 'uppercase', 
                fontSize: { xs: '15px', sm: '16px' }, 
                fontWeight: 600,
                color: 'primary.main'
              }}
            >
              
            </Typography>
            <List dense sx={{ p: 0 }}>
              {catalogLinks.map((item, idx) => (
                <ListItem key={idx} disablePadding sx={{ py: 0 }}>
                  <ListItemButton 
                    component={Link} 
                    to={item.to} 
                    sx={{ 
                      py: 0, 
                      px: 0, 
                      color: 'inherit', 
                      '&:hover': { color: 'secondary.main' },
                      minHeight: { xs: 32, sm: 36 }
                    }}
                  >
                    <ListItemText 
                      primary={item.label} 
                      sx={{ 
                        '& .MuiTypography-root': { 
                          fontSize: { xs: '13px', sm: '15px' }, 
                          fontWeight: 400,
                          whiteSpace: 'nowrap',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis'
                        } 
                      }} 
                    />
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
          </Grid>
          <Grid item xs={12} md={4}>
            <Typography 
              variant="subtitle1" 
              sx={{ 
                mb: { xs: 1, sm: 2 }, 
                textTransform: 'uppercase', 
                fontSize: { xs: '15px', sm: '16px' }, 
                fontWeight: 600,
                color: 'primary.main'
              }}
            >
               
            </Typography>
            <List dense sx={{ p: 0 }}>
              {infoLinks.map((item, idx) => (
                <ListItem key={idx} disablePadding sx={{ py: 0 }}>
                  <ListItemButton 
                    component={Link} 
                    to={item.to} 
                    sx={{ 
                      py: 0, 
                      px: 0, 
                      color: item.highlight ? 'secondary.main' : 'inherit', 
                      fontWeight: item.highlight ? 700 : 400,
                      textTransform: item.highlight ? 'uppercase' : 'none',
                      transition: 'color 0.2s', 
                      '&:hover': { color: '#fff' }, 
                      minHeight: { xs: 32, sm: 36 }
                    }}
                  >
                    <ListItemText 
                      primary={item.label} 
                      sx={{ 
                        '& .MuiTypography-root': { 
                          fontSize: { xs: '13px', sm: '15px' }, 
                          fontWeight: item.highlight ? 700 : 400, 
                          textTransform: item.highlight ? 'uppercase' : 'none',
                          whiteSpace: 'nowrap',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis'
                        } 
                      }} 
                    />
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
            <Box sx={{ mt: { xs: 2, sm: 3 } }}>
              <Box sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                mb: 1,
                fontSize: { xs: '13px', sm: '15px' }
              }}>
                <PhoneIcon sx={{ fontSize: { xs: 16, sm: 18 }, mr: 1 }} />
                <Typography component="span">+7 926 181-01-42</Typography>
              </Box>
              <Box sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                mb: 1,
                fontSize: { xs: '13px', sm: '15px' }
              }}>
                <EmailIcon sx={{ fontSize: { xs: 16, sm: 18 }, mr: 1 }} />
                <Typography component="span">nvvideo.ru@gmail.com</Typography>
              </Box>
            </Box>
          </Grid>
          <Grid item xs={12} md={4}>
            <Typography 
              variant="subtitle1" 
              sx={{ 
                mb: { xs: 1, sm: 2 }, 
                textTransform: 'uppercase', 
                fontSize: { xs: '15px', sm: '16px' }, 
                fontWeight: 600,
                color: 'primary.main'
              }}
            >
              
            </Typography>
            <Box sx={{ 
              display: 'flex', 
              flexDirection: 'column',
              gap: { xs: 1, sm: 1.5 }
            }}>
              <Typography sx={{ fontSize: { xs: '13px', sm: '15px' } }}>
                : .. - . 19, <br /> ,  17b
              </Typography>
              <Typography sx={{ fontSize: { xs: '13px', sm: '15px' } }}>
                 :  9.00-21.00
              </Typography>
              <Typography sx={{ fontSize: { xs: '13px', sm: '15px' } }}>
                : 772317907300   ..
              </Typography>
            </Box>
          </Grid>
        </Grid>
        <Box sx={{ textAlign: 'center', mt: 2, fontSize: { xs: '0.9rem', sm: '1rem' } }}>
           {new Date().getFullYear()} NVVIDEO.   .
        </Box>
      </Container>
    </Box>
  );
} 